import { extractImage } from './ExtractImage';
import { EVT } from '../EVT';
import { Utils } from '../utils/Utils';
class ToGIF {
    constructor() {
        this.gifWorkerUrl = '';
        this.loadWorkerJS();
    }
    async loadWorkerJS() {
        // 添加 gif 的 worker 文件
        let gifWorker = await fetch(chrome.runtime.getURL('lib/gif.worker.js'));
        const gifWorkerBolb = await gifWorker.blob();
        this.gifWorkerUrl = URL.createObjectURL(gifWorkerBolb);
    }
    // 转换成 GIF
    async convert(file, info) {
        return new Promise(async (resolve, reject) => {
            // 配置 gif.js
            let gif = new GIF({
                workers: 4,
                quality: 10,
                workerScript: this.gifWorkerUrl,
            });
            // 绑定渲染完成事件
            gif.on('finished', (file, data) => {
                data = null;
                EVT.fire('convertSuccess');
                resolve(file);
            });
            // 获取解压后的图片数据
            let base64Arr = await extractImage
                .extractImageAsDataURL(file, info)
                .catch(() => {
                reject(new Error('Start error'));
            });
            if (!base64Arr) {
                return;
            }
            // 生成每一帧的数据
            let imgList = await this.getFrameData(base64Arr);
            // 添加帧数据
            for (let index = 0; index < imgList.length; index++) {
                gif.addFrame(imgList[index], {
                    delay: info.frames[index].delay,
                });
            }
            base64Arr = null;
            imgList = null;
            // 渲染 gif
            gif.render();
        });
    }
    // 添加每一帧的数据
    async getFrameData(imgFile) {
        const resultList = [];
        return new Promise(async (resolve, reject) => {
            for (const base64 of imgFile) {
                const img = await Utils.loadImg(base64);
                resultList.push(img);
            }
            resolve(resultList);
        });
    }
}
const toGIF = new ToGIF();
export { toGIF };
