import { EVT } from './EVT';
import { Colors } from './config/Colors';
import { theme } from './Theme';
import { lang } from './Lang';
import { bg } from './BG';
// 简单的消息框
class MsgBox {
    constructor() {
        this.typeColor = {
            success: Colors.textSuccess,
            warning: Colors.textWarning,
            error: Colors.textError,
        };
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.showMsg, (ev) => {
            const msg = ev.detail.data;
            this.create(msg);
        });
    }
    show(msg, arg) {
        this.create(Object.assign({}, arg, { msg: msg }));
    }
    success(msg, arg) {
        this.create(Object.assign({ color: this.typeColor.success }, arg, { msg: msg }));
    }
    warning(msg, arg) {
        this.create(Object.assign({ color: this.typeColor.warning }, arg, { msg: msg }));
    }
    error(msg, arg) {
        this.create(Object.assign({ color: this.typeColor.error }, arg, { msg: msg }));
    }
    create(data) {
        const wrap = document.createElement('div');
        wrap.classList.add('xz_msg_box');
        let colorStyle = '';
        if (data.color) {
            colorStyle = `style="color:${data.color}"`;
        }
        wrap.innerHTML = `
        <p class="title" ${colorStyle}>${data.title || ''}</p>
        <p class="content" ${colorStyle}>${data.msg}</p>
        <button class="btn" type="button">${data.btn || lang.transl('_确定')}</button>
      `;
        theme.register(wrap);
        lang.register(wrap);
        const btn = wrap.querySelector('.btn');
        if (btn) {
            wrap.addEventListener('click', (ev) => {
                ev.stopPropagation();
            });
            btn.addEventListener('click', () => {
                this.remove(wrap);
            });
            window.addEventListener(EVT.list.closeCenterPanel, () => {
                this.remove(wrap);
            });
        }
        document.body.append(wrap);
        btn.focus();
        bg.useBG(wrap);
    }
    remove(el) {
        el && el.parentNode && el.parentNode.removeChild(el);
    }
}
const msgBox = new MsgBox();
export { msgBox };
