//初始化小说系列作品页面
import { InitPageBase } from '../crawl/InitPageBase';
import { Colors } from '../config/Colors';
import { options } from '../setting/Options';
import { store } from '../store/Store';
import { Tools } from '../Tools';
import { API } from '../API';
import { states } from '../store/States';
class InitNovelSeriesPage extends InitPageBase {
    constructor() {
        super();
        this.seriesId = '';
        this.limit = 30;
        this.last = 0;
        this.init();
    }
    addCrawlBtns() {
        Tools.addBtn('crawlBtns', Colors.bgBlue, '_抓取系列小说').addEventListener('click', () => {
            this.readyCrawl();
        });
    }
    addAnyElement() {
        Tools.addBtn('crawlBtns', Colors.bgBlue, '_合并系列小说').addEventListener('click', () => {
            states.mergeNovel = true;
            this.readyCrawl();
        });
    }
    initAny() { }
    setFormOption() {
        // 隐藏“个数/页数”选项
        options.hideOption([1]);
    }
    getWantPage() { }
    nextStep() {
        this.seriesId = API.getURLPathField('series');
        this.getIdList();
    }
    async getIdList() {
        const seriesData = await API.getNovelSeriesData(this.seriesId, this.limit, this.last, 'asc');
        const list = seriesData.body.seriesContents;
        for (const item of list) {
            store.idList.push({
                type: 'novels',
                id: item.id,
            });
        }
        this.last += list.length;
        // 如果这一次返回的作品数量达到了每批限制，可能这次没有请求完，继续请求后续的数据
        if (list.length === this.limit) {
            this.getIdList();
        }
        else {
            this.getIdListFinished();
        }
    }
    resetGetIdListStatus() {
        this.seriesId = '';
        this.last = 0;
    }
}
export { InitNovelSeriesPage };
