import { EVT } from '../EVT';
import { settings } from '../setting/Settings';
import { toWebM } from './ToWebM';
import { toGIF } from './ToGIF';
import { toAPNG } from './ToAPNG';
import { msgBox } from '../MsgBox';
import { lang } from '../Lang';
// 控制动图转换
class ConvertUgoira {
    constructor() {
        this.downloading = true; // 是否在下载。如果下载停止了则不继续转换后续任务，避免浪费资源
        this._count = 0; // 统计有几个转换任务
        this.maxCount = 1; // 允许同时运行多少个转换任务
        this.setMaxCount();
        this.bindEvents();
    }
    bindEvents() {
        window.addEventListener(EVT.list.downloadStart, () => {
            this.downloading = true;
        });
        [EVT.list.downloadPause, EVT.list.downloadStop].forEach((event) => {
            window.addEventListener(event, () => {
                this.downloading = false;
            });
        });
        // 设置发生变化时
        window.addEventListener(EVT.list.settingChange, (ev) => {
            const data = ev.detail.data;
            if (data.name === 'convertUgoiraThread') {
                this.setMaxCount();
            }
        });
        window.addEventListener(EVT.list.convertSuccess, () => {
            this.complete();
        });
        window.addEventListener(EVT.list.readZipError, () => {
            this.complete();
        });
        // 如果转换动图时页面被隐藏了，则显示一次提示
        document.addEventListener('visibilitychange', () => {
            this.checkHidden();
        });
    }
    setMaxCount() {
        this.maxCount =
            settings.convertUgoiraThread > 0 ? settings.convertUgoiraThread : 1;
    }
    set count(num) {
        this._count = num;
        EVT.fire('convertChange', this._count);
        this.checkHidden();
    }
    async start(file, info, type) {
        return new Promise(async (resolve, reject) => {
            const t = window.setInterval(async () => {
                if (this._count < this.maxCount) {
                    window.clearInterval(t);
                    if (!this.downloading) {
                        return;
                    }
                    this.count = this._count + 1;
                    if (type === 'gif') {
                        resolve(toGIF.convert(file, info));
                    }
                    else if (type === 'png') {
                        resolve(toAPNG.convert(file, info));
                    }
                    else {
                        // 如果没有 type 则默认使用 webm
                        resolve(toWebM.convert(file, info));
                    }
                }
            }, 200);
        });
    }
    complete() {
        this.count = this._count - 1;
    }
    // 转换成 WebM
    async webm(file, info) {
        return await this.start(file, info, 'webm');
    }
    // 转换成 GIF
    async gif(file, info) {
        return await this.start(file, info, 'gif');
    }
    // 转换成 APNG
    async apng(file, info) {
        return await this.start(file, info, 'png');
    }
    checkHidden() {
        if (this._count > 0 && document.visibilityState === 'hidden') {
            const name = 'tipConvertUgoira';
            const test = sessionStorage.getItem(name);
            if (test === null) {
                msgBox.warning(lang.transl('_转换动图时页面被隐藏的提示'));
                sessionStorage.setItem(name, '1');
            }
        }
    }
}
const convertUgoira = new ConvertUgoira();
export { convertUgoira };
