import { EVT } from './EVT';
import { settings } from './setting/Settings';
class BoldKeywords {
    constructor(wrap) {
        this.className = 'showBlobKeywords';
        this.wrap = wrap;
        this.bindEvent();
        this.setClassName();
    }
    bindEvent() {
        window.addEventListener(EVT.list.settingChange, (ev) => {
            const data = ev.detail.data;
            if (data.name === 'boldKeywords') {
                this.setClassName();
            }
        });
    }
    setClassName() {
        this.wrap.classList[settings.boldKeywords ? 'add' : 'remove'](this.className);
    }
}
export { BoldKeywords };
