import { EVT } from '../EVT';
import { store } from '../store/Store';
// 下载状态列表
class DownloadStates {
    constructor() {
        this.states = [];
        this.bindEvents();
    }
    bindEvents() {
        // 初始化下载状态
        const evs = [EVT.list.crawlFinish, EVT.list.resultChange];
        for (const ev of evs) {
            window.addEventListener(ev, () => {
                this.init();
            });
        }
    }
    // 创建新的状态列表
    init() {
        this.states = new Array(store.result.length).fill(-1);
    }
    // 统计下载完成的数量
    downloadedCount() {
        let count = 0;
        const length = this.states.length;
        for (let i = 0; i < length; i++) {
            if (this.states[i] === 1) {
                count++;
            }
        }
        return count;
    }
    // 接受传入的状态数据
    // 目前只有在恢复下载的时候使用
    replace(states) {
        this.states = states;
    }
    // 恢复之前的下载任务
    // 这会把之前的“下载中”标记复位到“未开始下载”，以便再次下载
    resume() {
        const length = this.states.length;
        for (let i = 0; i < length; i++) {
            if (this.states[i] === 0) {
                this.setState(i, -1);
            }
        }
    }
    // 获取第一个“未开始下载”标记的索引
    getFirstDownloadItem() {
        const length = this.states.length;
        for (let i = 0; i < length; i++) {
            if (this.states[i] === -1) {
                this.setState(i, 0);
                return i;
            }
        }
        return undefined;
    }
    // 设置已下载列表中的标记
    setState(index, value) {
        this.states[index] = value;
    }
    clear() {
        this.states = [];
    }
}
const downloadStates = new DownloadStates();
export { downloadStates };
